@font-face {
  font-family:Heebo-Regular ;
  src: url('./assets/fonts/Heebo-Regular.ttf');
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Heebo-Regular;
}

#root {
  width: 100%;
  height: 100%;
}

html, body {
  width: 100%;
  height: 100%;
}
.navbar-brand{
  width: 100px;
  height: 80px;

}
.navbar-brand img{
  width: 100%;
  height: 100%;
  object-fit: cover;
}

input{
  text-align: center;
}